.section {
    margin-bottom: 5rem;
    overflow: hidden;
}

.title-wrapper {
    display: flex;
    position: relative;
    margin-bottom: 3rem;
    align-items: center;
    gap: 1rem;

}

.devider {
    height: 100%;
    width: 4px;
    border-radius: 3px;
    background-color: var(--clr-primary);
    position: absolute;
}

.title {
    color: var(--clr-white);
    font-size: 2rem;
    text-shadow: 1px 1px 0.5px rgba(0, 0, 0, 0.8);
    margin-left: 1.1rem;
}

.title-wrapper i {
    margin-top: 0.3rem;
    color: var(--clr-primary);
    font-size: 2rem;
    transition: all 0.6s ease-in-out;
}

.title-wrapper i:hover {
    color: var(--clr-white);
}

.series,
.movies {
    box-sizing: border-box;
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 0 0.5rem;
    margin-bottom: 0.7rem;
}

.no-results {
    color: var(--clr-white);
    font-size: 1.5rem;
    text-shadow: 1px 1px 0.5px rgba(0, 0, 0, 0.8);
}

@media screen and (max-width:890px) {
    .movies,
    .series {
        flex-wrap: wrap;
        justify-content: center;
        gap: 2rem;
    }

}

@media screen and (max-width:790px) {
    .movies,
    .series {
        gap: 3rem;
    }

}

