.page-wrapper {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    color: var(--clr-white);


}


.background::before {
    background: radial-gradient(circle, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 1) 60%);
    width: 100%;
    position: absolute;
    left: 0;
    content: '';
    top: 0;
    height: 500px;
    z-index: 1;
}

.bgn-img {
    width: 60rem;
    height: 30rem;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

.background::after {
    width: 100%;
    position: absolute;
    left: 0;
    content: '';
    top: 250px;
    height: 490px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 55%, rgba(0, 0, 0, 0) 100%);
}


.details-section {
    z-index: 10;
    position: relative;
    top: 400px;
    padding: 2rem 4rem;
    background-color: rgba(24, 24, 24, 0.397);
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
    border-radius: 0.8rem;
    box-sizing: border-box;
    /* left: 50%;
    transform: translateX(-50%); */
    /* overflow: hidden; */

}

.details-section .header {
    display: flex;
    top: 0;
    margin-bottom: 4rem;
    justify-content: space-between;
    align-items: center;
}

.header .title {
    font-size: 3rem;
    color: var(--clr-white);
    margin-bottom: 0.5rem;
}

.header .small-info {
    display: flex;
    gap: 0.7rem;
    align-items: center;
    color: var(--clr-grey);
}

.header .small-info i {
    font-size: 0.3rem;
}


.header .rating-wrapper {
    display: flex;
    gap: 1.5rem;
    justify-content: flex-end;
}

.rating-wrapper .current-rating>div,
.rating-wrapper .your-rating>div {
    display: flex;
    gap: 0.5rem;
    align-items: center;
}

.current-rating .rating-points,
.your-rating {
    font-size: 1.2rem;
    font-weight: 600;
}

.current-rating .users-rated {
    color: var(--clr-grey);
}

.current-rating i {
    color: var(--clr-secondary);
    font-size: 2rem;
}

.current-rating .rating-content {
    display: flex;
    align-items: center;
    gap: 0.3rem;
}

.your-rating>div>button {
    background-color: transparent;
    border: none;
}

.your-rating i {
    color: var(--clr-primary);
    font-size: 2rem;
    cursor: pointer;
    transition: all 0.6s ease-in-out;
}

.your-rating i:hover {
    scale: 1.1;
}

.rating-label {
    text-align: center;
    color: var(--clr-grey);
    font-weight: 600;
    font-size: 0.8rem;
    margin-bottom: 0.3rem;
}




.details-main {
    /* overflow: hidden; */
}

.img-wrapper {
    /* overflow: hidden; */
    height: 28rem;
}

.img-wrapper img {
    object-fit: contain;
    width: 100%;
    height: 100%;
    /* border-radius: 4rem; */
    overflow: hidden;
    /* background-color: var(--clr-light-black); */

}

.img-container {
    overflow: hidden;
}


.details-main p {
    flex-basis: 70%;
}



*::-webkit-scrollbar {
    width: 0;
}

*::-webkit-scrollbar-track {
    background: transparent;
}

*::-webkit-scrollbar-thumb {
    background: transparent;
    border: none;
}

* {
    -ms-overflow-style: none;
}

.carousel ol,
.carousel li {
    list-style: none;
    margin: 0;
    margin-right: 0.3rem;
}

/* .carousel {
    position: relative;
    padding-top: 75%;
    filter: drop-shadow(0 0 10px #0003);
    perspective: 100px;
} */

.carousel__viewport {

    display: flex;
    overflow-x: scroll;
    counter-reset: item;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
}

.carousel__slide {
    position: relative;
    flex: 0 0 100%;
    width: 100%;
    counter-increment: item;
}


.carousel__snapper {

    scroll-snap-align: center;
    height: 28rem;
}



.carousel__navigation {
    position: absolute;
    right: 0;
    left: 0;
    text-align: center;
    margin-top: -1.2rem;
}

.carousel__navigation-list,
.carousel__navigation-item {
    display: inline-block;
}


.carousel__navigation-button {
    display: inline-block;
    width: 1rem;
    height: 1rem;
    background-color: transparent;
    background-clip: content-box;
    border: 0.15rem solid var(--clr-white);
    border-radius: 50%;
    font-size: 0;
    transition:
        background-color 0.6s ease-in-out, transform 0.3s;
}

.carousel__navigation-button:hover {
    background-color: var(--clr-white);

}

.carousel__navigation-button.selected {
    background-color: var(--clr-white);

}

.carousel__navigation__sides {

    position: absolute;
    margin-top: -17rem;
    width: 75%;
    left: 50%;
    transform: translateX(-50.3%);
}

.carousel__navigation__sides .arrows-container {
    display: flex;
    justify-content: space-between;

}

.carousel__navigation-arrow {
    width: 4rem;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    border-radius: 0.3rem;
    transition: all 0.3s ease-in-out;
}

.carousel__navigation-arrow.hidden {
    visibility: hidden;
}


.carousel__navigation-arrow i {
    font-size: 3rem;
    color: var(--clr-white);
}

.carousel__navigation-arrow:hover {
    background-color: var(--clr-grey-bgn);
}




.content {
    margin-top: 4rem;
}

.desc,
.director,
.reviews {
    font-size: 1.1rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
    line-height: 1.3;
}


.director>span,
.desc>span,
.reviews>span {
    color: var(--clr-grey);
    margin-right: 0.4rem;
    font-size: 1rem;
}

.genres {
    margin: 1.5rem 0 2rem 0;
    color: var(--clr-grey);
    display: flex;
    flex-wrap: wrap;
}

.genres .genre {
    background: radial-gradient(circle, rgb(36, 36, 36) 0%, rgba(41, 41, 41, 0.6) 100%);
    padding: 0.4rem 0.8rem;
    border-radius: 0.5rem;
    margin-right: 0.7rem;
    margin-bottom: 0.3rem;
}


.actor-list .actors {
    display: flex;
    flex-direction: column;
    list-style: none;
    gap: 1.5rem;
    margin-bottom: 4rem;
}

.actors .actor {
    display: flex;
    gap: 2rem;
    align-items: center;
}

.actor .img {
    height: 60px;
    width: 60px;
    object-fit: cover;
    border-radius: 100%;
    overflow: hidden;
}

.actor p:nth-child(1) {
    margin-bottom: 0.3rem;
}

.actor p {
    font-size: 1rem;
    font-weight: 600;
    display: flex;
    align-items: center;
}

.actor p>span {
    font-size: 0.8rem;
    color: var(--clr-grey);
    margin-right: 0.4rem;
}

.title-wrapper {
    display: flex;
    position: relative;
    margin-bottom: 1rem;
    align-items: center;
    gap: 1rem;

}

.devider {
    height: 100%;
    width: 4px
    ;
    border-radius: 3px;
    background-color: var(--clr-primary);
    position: absolute;
}

.title-wrapper .title {
    color: var(--clr-white);
    font-size: 2rem;
    text-shadow: 1px 1px 0.5px rgba(0, 0, 0, 0.8);
    margin-left: 1.1rem;
}

.title-wrapper i {
    margin-top: 0.3rem;
    color: var(--clr-primary);
    font-size: 2rem;
    transition: all 0.6s ease-in-out;
}

.title-wrapper i:hover {
    color: var(--clr-white);
}

.admin-buttons {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}

.admin-buttons .edit {
    background-color: #d9a612;
    border-color: #d9a612;
}

@media screen and (min-width: 1630px) {
    .bgn-img {
        width: 73rem;
        height: 30rem;
    }

}

@media screen and (max-width: 870px) {
    .bgn-img {
        width: 55rem;
        height: 28rem;
    }
    .details-section {
        padding: 2rem 2rem;

    }
    .header .title {
        font-size: 2.5rem;
    }

    .header .small-info {
        gap: 0.5rem
    }
}

@media screen and (max-width: 770px) {
    .bgn-img {
        width: 52rem;
        height: 27rem;
    }
    .header .title {
        font-size: 2.2rem;
    }
}

@media screen and (max-width: 730px) {
    .bgn-img {
        width: 47rem;
    }

    .header .rating-wrapper {
        gap: 1rem;
    }
}

@media screen and (max-width: 670px) {
    .bgn-img {
        width: 43rem;
    }

    .details-section {
        padding: 1.5rem;

    }
    .header .title {
        font-size: 1.9rem;
    }
}

@media screen and (max-width: 600px) {
    .bgn-img {
        width: 37rem;
    }
    .header .rating-wrapper {
        gap: 0.6rem;
    }

    .background::before {
        top: -2rem;
    }
}

@media screen and (max-width: 550px) {
    .bgn-img {
        width: 33rem;
    }
    .rating-wrapper .current-rating>div,
.rating-wrapper .your-rating>div {
    display: flex;
    gap: 0.5rem;
    flex-direction: column;
    align-items: center;
}
}

@media screen and (max-width: 470px) {
    .bgn-img {
        width: 29rem;
    }
    .bgn-img {
        width: 26rem;
        top: 2rem;
    }

}

@media screen and (max-width: 410px) {
    .header .title {
        font-size: 1.6rem;
    }
    .bgn-img {
        width: 26rem;
        top: 4rem;

    }
    .details-section {
        padding: 1.2rem;

    }
}


@media screen and (max-width: 400px) {
    .current-rating i,
    .your-rating i {
        font-size: 1.7rem;
    }

    .current-rating .rating-points,
    .your-rating .rating-points {
        font-size: 1rem;
    }

    .bgn-img {
        width: 24rem;
    }
}